.loadingWrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top:100px;
}

.spinner{
    margin-bottom:20px;
    animation:spin 1s ease-in infinite;
}

@keyframes spin{
    100%{
        transform: rotate(360deg);
    }
}